<template>
  <div class="container">
    <h2 class="text-center">帳戶管理-編輯</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.go(-1)">返回上一頁</button>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <div class="row">
      <div class="col-12">
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="studentIDInput" v-model="user.studentID" placeholder="學號" disabled />
          <label for="studentIDInput">學號</label>
        </div>
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="userNameInput" v-model="user.name" placeholder="名稱" disabled />
          <label for="userNameInput">名稱</label>
        </div>
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="accountInput" v-model="user.account" placeholder="帳號" disabled />
          <label for="accountInput">帳號</label>
        </div>
        <select class="form-select form-select-lg mb-3" aria-label=".form-select-lg example" v-model="user.ident">
          <option value="Admin">管理者</option>
          <option value="User">使用者</option>
          <option value="Register">未審核</option>
          <option value="Stop">停用</option>
        </select>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-success btn-lg" type="button" @click="sendEdit()">確認修改</button>
      </div>
    </div>
    <div class="col-12 d-grid gap-2 my-2">
      <button class="btn btn-warning btn-lg" type="button" @click="resetExamExp()">重置學習紀錄</button>
    </div>
    <div class="col-12 d-grid gap-2 my-2">
      <button class="btn btn-danger btn-lg" type="button" @click="deleteUser()">刪除帳戶</button>
    </div>
  </div>
</template>

<script>
import router from "@/router";
import Alert from "@/components/Alert.vue";

export default {
  name: "AccountEdit",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: 0,
      user: {
        studentID: "",
        name: "",
        account: "",
        ident: "",
      },
    };
  },
  methods: {
    sendEdit() {
      this.alertVisible = true;
      this.MSG.MODE = "OKCancel";
      this.funcNum = 1;
      this.MSG.TYPE = "warning";
      this.MSG.TITLE = "系統警告";
      this.MSG.TEXT = ["是否要修改帳戶參數？"];
    },
    resetExamExp() {
      this.alertVisible = true;
      this.MSG.MODE = "OKCancel";
      this.funcNum = 3;
      this.MSG.TYPE = "warning";
      this.MSG.TITLE = "系統警告";
      this.MSG.TEXT = ["是否要重置學習紀錄？"];
    },
    deleteUser() {
      this.alertVisible = true;
      this.MSG.MODE = "OKCancel";
      this.funcNum = 5;
      this.MSG.TYPE = "danger";
      this.MSG.TITLE = "系統警告";
      this.MSG.TEXT = ["是否要刪除帳戶？"];
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  created() {
    // 載入資料
    this.user = this.$store.state.accountEdit;
  },
  mounted() {
    // 清除Vuex帳戶資料
    this.$store.commit("SET_ACCOUNT_EDIT", {});
  },
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === 1) {
            // 功能1-[修改用戶參數]
            // 頁面處理
            this.hideAlert();
            this.$http
              .post("api/accountManage/modifyUser", this.user)
              .then((response) => {
                if (response.data.code === 200) {
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly";
                  this.funcNum = 2;
                  this.MSG.TYPE = "primary";
                  this.MSG.TITLE = "系統提示";
                  this.MSG.TEXT = ["已修改完成！"];
                }
              })
              .catch((error) => {
                console.log(error);
              });
          } else if (this.funcNum === 2) {
            // 功能2-[修改用戶參數-後續處理]
            this.hideAlert();
            this.$router.go(-1);
          } else if (this.funcNum === 3) {
            // 功能3-[重置學習紀錄]
            // 頁面處理
            this.hideAlert();
            this.$http
              .post("api/accountManage/resetExamExp", this.user)
              .then((response) => {
                if (response.data.code === 200) {
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly";
                  this.funcNum = 4;
                  this.MSG.TYPE = "primary";
                  this.MSG.TITLE = "系統提示";
                  this.MSG.TEXT = ["已重置完成！"];
                } else {
                  console.log(response.data.code);
                }
              })
              .catch((error) => {
                console.log(error);
              });
          } else if (this.funcNum === 4) {
            // 功能4-[重置學習紀錄-後續處理]
            // 關閉訊息提示框
            this.hideAlert();
          } else if (this.funcNum === 5) {
            // 功能5-[刪除用戶]
            // 頁面處理
            this.hideAlert();
            this.$http
              .post("api/accountManage/deleteUser", this.user)
              .then((response) => {
                if (response.data.code === 200) {
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly";
                  this.funcNum = 6;
                  this.MSG.TYPE = "primary";
                  this.MSG.TITLE = "系統提示";
                  this.MSG.TEXT = ["已刪除完成！"];
                }
              })
              .catch((error) => {
                console.log(error);
              });
          } else if (this.funcNum === 6) {
            // 功能6-[刪除用戶-後續處理]
            this.hideAlert();
            this.$router.go(-1);
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
  },
};
</script>

<style lang="scss"></style>
